import "./login.css";
import { useEffect, useState } from "react";
import GoogleLogin from 'react-google-login';
import jwt_decode from "jwt-decode";
import App from '../../App';
import axios from "axios";
import { cookieManager } from "../../managers/cookieManager";
import { httpManager } from "../../managers/httpmanager";

const API_BASE_URL = "http://localhost:3005";

const LoginComponent = () => {
    const [userInfo, setUserInfo] = useState()
    useEffect(()=>{
        const userData = cookieManager.getUserInfo()
        if(userData) setUserInfo(userData);
    }, []);
    const handleGoogleResponse = async(responseData) => {
        console.log("Encoded JWT ID token: "+responseData.credential);
        var userObject = jwt_decode(responseData.credential);
        console.log(userObject);
        await httpManager.createUser({
            email: userObject.email,
            name: userObject.name,
            profilePic: userObject.picture
        });
        setUserInfo(userObject);
        cookieManager.setUserInfo(userObject);
    };
    useEffect(() => {
        /* global google */
        google.accounts.id.initialize({
            client_id: "843833453016-puv0ghcrt25pft0qr0j50g8qts5u32fo.apps.googleusercontent.com",
            callback: handleGoogleResponse
        });
        google.accounts.id.renderButton(
            document.getElementById("signInDiv"),
            {theme: "outline", size: "large"}
        );
    }, []);
    
    return (
        <>
        {userInfo?<App userInfo={userInfo}/>:
        <div className="Container">
        <div className="header">MESSAGING APP</div>
        <div className="cardView">
            <div className="instructions">
                <span className="Heading">To use this React Messaging App on your computer:</span>
                <ol>
                    <li>You need to sign in using your Google account</li>
                    <li>You can log out anytime from the web</li>
                    <li>Click on Sign in with Google to continue using the Messaging App</li>
                </ol>
                <div id="signInDiv"></div>
            </div>
            <img src = "https://www.kindpng.com/picc/m/32-320995_transparent-messaging-icon-png-communication-icon-png-download.png"></img>
        </div>
        </div>
        }
        </>
    );
};
export default LoginComponent;