import './App.css';
import Sidebar from './components/Sidebar';
import Chat from './components/Chat';
import { useState } from 'react';
function App(props) {
	const {userInfo}=props
	const [selectedChat, setChat] = useState();
	const [refreshContactList, toggleRefreshContactList] = useState(false);
	return (
    	<div className="app">
			<div className="app_body">
				<Sidebar 
					setChat={setChat} 
					userInfo={userInfo} 
					refreshContactList={refreshContactList}
				/>
				{selectedChat ? 
					(<Chat 
						selectedChat={selectedChat} 
						userInfo={userInfo} 
						refreshContactList = {() => toggleRefreshContactList(!refreshContactList)}
					/>) : 
					(
						<div className='placeholder'>
							<img className='chatPlaceholder' src="https://www.kindpng.com/picc/m/296-2960926_messaging-and-positioning-icon-hd-png-download.png"></img>
							<span>Full stack messaging app using MERN</span>
							Frontend: React, Backend: MongoDB, ExpressJS, Node.JS
						</div>
					)
				}
			</div>
    	</div>
  	);
}

export default App;
