import DonutLargeIcon from '@mui/icons-material/DonutLarge';
import ChatIcon from '@mui/icons-material/Chat';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { Avatar, IconButton } from '@mui/material';
import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';
import "./Sidebar.css";
import SidebarChat from './SidebarChat';
import {contactList} from "../mockData";
import {useEffect, useState} from "react";
import utility from '../utility';
import httpManager from '../managers/httpmanager';


function Sidebar(props)
{
    const {userInfo, refreshContactList}=props;
    const [searchString, setSearchString] = useState("");
    const [searchResult, setSearchResult] = useState("");
    const [contactList, setContactList] = useState([]);

    const refreshContacts = async() => {
        const contactListData = await httpManager.getChannelList(userInfo.email);
        setContactList(contactListData.data.responseData);
        setSearchString();
        setSearchResult();
    };

    useEffect(() => {
        refreshContacts()
    }, [refreshContactList]);

    const onSearchTextChanged = async(searchText) => {
        setSearchString(searchText);
        if(!utility.validateEmail(searchText)) return;
        const userData = await httpManager.searchUser(searchText);
        //console.log("=====", userData);
        if(userData.data?.success) setSearchResult(userData.data.responseData);
    }
    return (
        <div className = "sidebar">
            <div className='sidebar_header'>
                <IconButton>
                <Avatar src={userInfo.picture||"https://www.svg.com/img/gallery/things-in-red-dead-redemption-2-that-made-us-want-to-rage-quit/intro-1549401399.webp"}/>
                </IconButton>
                <div className='sidebar_headerRight'>
                    <IconButton>
                        <DonutLargeIcon />
                    </IconButton>
                    <IconButton>
                        <ChatIcon />
                    </IconButton>
                    <IconButton>
                        <MoreVertIcon />
                    </IconButton>
                </div>
            </div>
            <div className='sidebar_search'>
                <div className='sidebar_searchContainer'>
                    <SearchOutlinedIcon />
                    <input placeholder='Search or start new chat...' type="text" 
                    value={searchString}
                    onChange={(e)=>onSearchTextChanged(e.target.value)}
                    />
                </div>
            </div>
            {searchResult && (
                <div className='searchResults'>
                    <SidebarChat userData={searchResult} setChat={props.setChat}/>
                </div>
            )}
            <div className='sidebar_chats'>
                {contactList.map((userData) => (
                <SidebarChat userData={userData} setChat={props.setChat} userInfo={userInfo}/>
                ))}
            </div>
        </div>
    )
}
export default Sidebar;