import { Avatar, IconButton } from '@mui/material';
import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';
import AttachFileIcon from '@mui/icons-material/AttachFile';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import InsertEmoticonIcon from '@mui/icons-material/InsertEmoticon';
import MicIcon from '@mui/icons-material/Mic';
import Picker from 'emoji-picker-react';
import "./Chat.css";
import {messagesList} from "../mockData";
import { useEffect, useState } from 'react';
import httpManager from '../managers/httpmanager';

function Chat(props)
{
    const {selectedChat, userInfo, refreshContactList} = props;
    const [text, setText] = useState("");
    const [pickerVisible, togglePicker] = useState(false);
    const [messageList, setMessageList] = useState([]);
    const onEmojiClick = (event, emojiObj) => {
        setText(text+emojiObj.emoji)
        togglePicker(false);
    };
    useEffect(()=>{
        setMessageList(selectedChat.channelData.messages);
    }, [selectedChat])
    const onEnterPress = async(event)=>{
        let channelId = selectedChat.channelData._id;
        if(event.key === "Enter")
        {
            event.preventDefault();
            if(!messageList || !messageList.length)
            {
                const channelUsers = [
                    {
                        email: userInfo.email,
                        name: userInfo.name,
                        profilePic: userInfo.picture,
                    },
                    {
                        email: selectedChat.otherUser.email,
                        name: selectedChat.otherUser.name,
                        profilePic: selectedChat.otherUser.profilePic,
                    },
                ]
                const channelResponse = await httpManager.createChannel({channelUsers});
                channelId=channelResponse.data.responseData._id;
                
            }
            const messages = [...messageList];
            const msgReqData = {
                text,
                senderEmail: userInfo.email,
                addedOn: new Date().getTime(),
            }
            const messageResponse = await httpManager.sendMessage({channelId, messages: msgReqData});
            messages.push(msgReqData);
            setMessageList(messages);
            setText("");
            refreshContactList()
        }
    };
    return (
        <div className = "chat">
            <div className="chat_header">
                <IconButton>
                    <Avatar src={selectedChat.otherUser.profilePic}/>
                </IconButton>
                {selectedChat.otherUser.name}
                <div className='chat_headerinfo'>
                    
                </div>
                <div className='chat_headerRight'>
                    <IconButton>
                        <SearchOutlinedIcon />
                    </IconButton>
                    <IconButton>
                        <AttachFileIcon />
                    </IconButton>
                    <IconButton>
                        <MoreVertIcon />
                    </IconButton>
                </div>
            </div>
            <div className='chat_body'>
                {messageList?.map((messageData)=>(
                    <p className={`chat_msg ${(messageData.senderEmail === userInfo.email) && "chat_receiver"}`}>
                        <span className='chat_name'></span>
                        {messageData.text}
                        <span className='chat_timestamp'>{new Date(messageData.addedOn).toUTCString()}</span>
                    </p>
                ))}
                
            </div>
            <div className='chat_footer'>
                
                <IconButton onClick={()=>togglePicker(!pickerVisible)}><InsertEmoticonIcon /></IconButton>
                {pickerVisible && (<Picker 
                pickerStyle={{position:"absolute", bottom: "60px"}}
                onEmojiClick={onEmojiClick}
                emojiStyle="apple"
                />)}
                <form>
                    <input placeholder='Type a message' 
                    type='text' 
                    value={text} 
                    onKeyDown={onEnterPress}
                    onChange={(e)=>setText(e.target.value)}/>
                    <button type='submit'>Send a message</button>
                </form>
                <IconButton><MicIcon /></IconButton>
            </div>
        </div>
    );
}
export default Chat;