import {Avatar, IconButton} from "@mui/material"
import "./SidebarChat.css";
import {contactList} from "../mockData";

function SidebarChat(props)
{
    const {userData, setChat, userInfo} = props;
    const otherUser = userData.channelUsers?.find(
        (userObj) => userObj.email !== userInfo.email
    ) || userData;
    const lastMessage = userData.messages && userData.messages.length
    ?userData.messages[userData.messages.length-1]
    :{};
    return(
        <div className="sidebarChat" onClick={()=>setChat({channelData:userData, otherUser})}>
            <Avatar src={otherUser?.profilePic}/>
            <div className="sidebarChat_info">
                <span className="contactname">{otherUser?.name}</span>
                <span className="msgtext">{lastMessage?.text}</span>
                <span className="msgtime">{new Date(lastMessage?.addedOn).toUTCString()}</span>
            </div>
            
        </div>
    );
}
export default SidebarChat;